/* 
-------------------------------------------------- */

// 
.product-card {
  // 
  .product-name {
    font-size: $h1-font-size;
    line-height: 28px;
    color: $custom-gray-600;
  }
  // 
  .product-image {
    img {
      object-fit: cover;
    }
  }
  //  ()
  .sale-tag {
    top: 1rem;
    right: 1rem;
    font-size: 14px;
    line-height: 1rem;
  }
  // 
  .price {
    //  / 
    .original,
    .sale {
      line-height: $headings-line-height;
      color: $custom-gray-600;
    }
    .sale {
      color: $custom-gray-400;
    }
  }
}
